/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
h1 {
  color: #7A9F35;
  font-size: 2em;
  line-height: 2em;
}
.icon {
  display: inline-block;
  font-size: 1.5em;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
}
body {
  background-color: #882D60;
  background-position: 50% -50%;
  background-repeat: no-repeat;
  background-size: 250px;
  font-family: sans-serif;
  font-size: 87.5%;
  margin: 0;
  padding: 2em 0;
}
/*---
title:   Structure
section: Base CSS
---

Styles for elements such as `<section>` could go in this section.

*/
#main {
  width: 80%;
  padding: 0 1em;
}
section {
  color: #440026;
  background-color: #AA5585;
  line-height: 3em;
  margin: 2em 0;
  text-align: center;
  transform: scale(1);
  user-select: none;
}
footer {
  background-color: #7A9F35;
  bottom: 0;
  padding: 1em 0;
  position: fixed;
  text-align: center;
  width: 100%;
}
/*---
title:   Links
section: Base CSS
---

```example:html
<a href="#">This is a sample link</a>
<a href="#">This is another sample link</a>
<a href="#">This is a third sample link</a>
```

*/
a,
a:visited {
  border-radius: 5px;
  background-color: #440026;
  color: #AA5585;
  padding: 5px;
  text-decoration: none;
  text-transform: uppercase;
}
a:hover {
  background-color: #7A9F35;
  color: #AA5585;
}
li {
  cursor: pointer;
}
/*---
title:   Buttons
section: Input Controls
---

The default button style is pictured below.

```example:html
<button>Hello World</button>
```
*/
button {
  background-color: blue;
  border: none;
  border-radius: 15px;
  color: white;
  padding: 1em;
  transition: all 100ms;
  text-transform: uppercase;
}
button:hover {
  background-color: #3333ff;
  cursor: pointer;
}
nav {
  cursor: pointer;
  display: inline;
  background-color: #661141;
  padding: 10px;
  border-radius: 5px;
  color: #440026;
  margin: 10px;
  user-select: none;
}
.todo-container {
  display: flex;
  justify-content: space-between;
  font-size: 1.5em;
  align-items: center;
}
.cross-out {
  text-decoration: line-through;
}
.pencil:before {
  content: '\e905';
  font-family: 'icomoon';
}
.bin:before {
  content: '\e9ac';
  font-family: 'icomoon';
}
.checked:before {
  content: '\ea10';
  font-family: 'icomoon';
}
.unchecked:before {
  content: '\ea11';
  font-family: 'icomoon';
}
.plus:before {
  content: '\ea0a';
  font-family: 'icomoon';
}
#todo-icons .icon:hover,
.todo-check:hover {
  color: #7A9F35;
}
